  opacity: 1;
  transform: scale(1) !important;
}
.project__img-list__wrapper:before {
  transition: all 0.5s ease;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #333;
  transform: scale(0);
}
.project__img-list__wrapper:hover:before {
  opacity: 0.5;
  transform: scale(1);
}
.project__img-list__wrapper:after {
  transition: all 0.6s ease 0.2s;
  content: "";
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  bottom: 8px;
  border: 1px solid #aaa;
  background: #000;
  opacity: 0;
  transform: scale(0);
